import {
  reactive, computed, ref, toRefs, getCurrentInstance, onMounted, watch, SetupContext,
} from '@vue/composition-api';

const useCols = (inicColsNum:number) => {
  const _this:any = getCurrentInstance();
  const state = reactive({
    currentColsNum: inicColsNum,
  });
  const review = ref(null);
  watch(() => _this.colsNum, () => {
    if (!review.value || _this.colsNum < 16) {
      state.currentColsNum = _this.colsNum;
      return;
    }
    // 小于1600px的屏幕不能是16列，图标太小看不见
    state.currentColsNum = (review.value! as HTMLDivElement).clientWidth > 1600 ? _this.colsNum : 8;
  });
  const colWidth = computed(() => 100 / state.currentColsNum);
  const colWidthStyle = computed(() => ({
    width: `${colWidth.value}%`,
  }));
  return {
    ...toRefs(state),
    review,
    colWidth,
    colWidthStyle,
  };
};

export default useCols;
